import { GW2AccountInfo, GW2AccountSearchResult } from "./GW2AccountInfo";
import { CharacterEqTemplateResult, EqTemplateListResult, GW2API_Call } from "./GW2API_Call";

export class GW2ItemFindProcess{

    apiKey: string;
    itemID: string;




    constructor(apiKey: string, itemID: string){
        this.apiKey = apiKey;
        this.itemID = itemID;
    }

    async Execute() : Promise<GW2AccountSearchResult[]> {
        return new Promise((resolve,error)=>{
            let apiCall : GW2API_Call = new GW2API_Call(this.apiKey);
            // First get the character list
            apiCall.GetCharacterList().then((result)=>{
                const characters = result;

                const pendingTemplates : Promise<EqTemplateListResult>[] = [];
                for (let i = 0; i < characters.length; i++) {
                    const characterName = characters[i];
                    
                    const fetchEqTempaltes = new GW2API_Call(this.apiKey).GetEquipmentTemplates(characterName);
                    pendingTemplates.push(fetchEqTempaltes);
                }
           
                // Get EqTemplates of all characters
                return Promise.all(pendingTemplates)
                .then(results=>{
                    if(results == null || results.length == 0){
                        resolve([]);
                        return;
                    }

                    const pendingPopulatedEquipmentTemplates : Promise<CharacterEqTemplateResult>[] = []
                    // We have aquired all Templates, now aquire all Equipment in all of the templates
                    for (let j = 0; j < results.length; j++) {
                        const templateResult: EqTemplateListResult = results[j];
                        for (let k = 0; k < templateResult.TemplateList.length; k++) {
                            const templateID = templateResult.TemplateList[k];
                            const fetchEquipment = new GW2API_Call(this.apiKey).GetEquipmentFromTemplate(templateResult.CharacterName, templateID);
                            pendingPopulatedEquipmentTemplates.push(fetchEquipment);
                        }
                    }
                    // Fire all Lookups to get equipment data on every character
                    return Promise.all(pendingPopulatedEquipmentTemplates)
                    .then(eqData=>{

                        if(eqData == null || eqData.length == 0){
                            resolve([]);
                            return;
                        }
                        for (let i = 0; i < eqData.length; i++) {
                            const element = eqData[i];
                        }

                        // Compose account Object
                        let accountInfo : GW2AccountInfo = new GW2AccountInfo();
                        accountInfo.populateWithData(eqData);

                        // Find and return the item
                        let searchHits : GW2AccountSearchResult[] = accountInfo.FindItemInAccount(parseInt(this.itemID));
                        console.log(searchHits);
                        resolve(searchHits);

                    });
                    
                });
            }

            ).catch((error)=>{
                error("Error");
            }          
            );
        });
    }


}