import { render } from "@headlessui/react/dist/utils/render";
import React, { Component } from "react";
import { GW2API_Call } from "~/GW2Api/GW2API_Call";
import { LoaderFunction, useLoaderData } from 'remix'
import { useEffect, useState } from "react";
import { GW2ItemFindProcess } from "~/GW2Api/GW2ItemFindProcess";
import { GW2AccountInfo, GW2AccountSearchResult } from "~/GW2Api/GW2AccountInfo";
import { GW2Item } from "~/GW2Api/GW2Item";


export type ItemCard = {

    apiKey: string,
    itemID: string,

}

const useAPIData = (apikey,itemid) => {
    const [apiData, setApiData] = useState([])
    const [isLoading, setLoading] = useState(true)
    const [result, setResult] = useState<GW2AccountSearchResult[]>([])
    const [searchItem, setSearchItem] = useState<GW2Item>()

    useEffect(()=>{
        setLoading(true);

        // First, get info about the item we want to display
        let item : GW2Item = new GW2Item(itemid);
        item.populateFromAPI()
        .then(res=>{
            // The Item we are trying to find is valid! Save it then find where on the account it could be
            setSearchItem(res);

            let FindProcess : GW2ItemFindProcess = new GW2ItemFindProcess(apikey,itemid)

            // Find the item by it's number
            FindProcess.Execute().then(res=>{
                let results : GW2AccountSearchResult[] = [];
                setResult(res);

                setLoading(false);
            }).catch(err=>{

        });
     

        })
        .catch(err=>{
            setLoading(false);
            console.log("No Valid Item Found!");
        })
 
    },[]);

    return {apiData, isLoading, result, searchItem}
}


const ItemSearch = (props : ItemCard) => {
    const { apiData, isLoading,result, searchItem } = useAPIData(props.apiKey, props.itemID);
    return (
        <div className="bg-gray-800 border-solid rounded-2xl px-5 py-5">
        {isLoading ? (
            <p className="loading-text">Loading Data...</p>
        ) : (
            <div>
                <div className="bg-gray-300 border-solid rounded 1xl mx-1 my-1 h-1 ">
                <img src={searchItem?.IconUrl} className="h-full"></img>
                </div>
                {result.map(r=>(
                <div key={r.uuid}>
                </div>
            ))}
            </div>

        )}
    </div>
    );
};

export default ItemSearch;