import { resolvePath } from "react-router";

export type EqTemplateListResult ={
    CharacterName: string,
    TemplateList: string[]
}

export type CharacterEqTemplateResult = {
    CharacterName: string,
    EqTemplateId: string,
    Equipment: object
}

export type ItemDataResult = {
    ID: number;
    Name: string;
    IconUrl: string;
    Type: string;
}

export class GW2API_Call {

    ApiKey: string;
    URI: string;
    IsDone: boolean;
    Response: string;

    constructor(apiKey: string){
        this.ApiKey = apiKey;
        this.URI = "";
        this.Response = "test";
        this.IsDone = false;
    }

    async call() : Promise<string>{
        return new Promise((resolve,error)=>{
            let context = this;
            let endpoint = this.URI+"?access_token="+this.ApiKey;
            return fetch(endpoint, {
                method: 'GET',
                redirect: 'follow'
            }).then(response =>{
                context.IsDone = true;
                response.text().then(res=>{
                    context.Response = res;
                    resolve(res);
                });
            }).catch(err=> error("Error: " + err));
        })     
    }

    async callNoAuth() : Promise<string>{
        return new Promise((resolve,error)=>{
            let context = this;
            let endpoint = this.URI;
            return fetch(endpoint, {
                method: 'GET',
                redirect: 'follow'
            }).then(response =>{
                context.IsDone = true;
                response.text().then(res=>{
                    context.Response = res;
                    resolve(res);
                });
            }).catch(err=> error("Error: " + err));
        })
    }

    GetCharacterList() : Promise<string[]>{
        return new Promise((res,err)=>{
            this.URI = "https://api.guildwars2.com/v2/characters";
            this.call().then((resolve)=>{
                res(JSON.parse(this.Response));
            })
            .catch(()=>{err("Error")});
        }) 
    }

    GetEquipmentTemplates(characterName :string) : Promise<EqTemplateListResult> {
        return new Promise<EqTemplateListResult>((resolve,error)=>{
            this.URI = "https://api.guildwars2.com/v2/characters/" + characterName + "/equipmenttabs";
            this.call().then((res)=>{
                const result : EqTemplateListResult = {CharacterName : characterName, TemplateList : JSON.parse(res)};
                resolve(result);
            }).catch(err=>error(err));
        });
    }

    GetEquipmentFromTemplate(characterName: string, templateId: string ) : Promise<CharacterEqTemplateResult>{
        return new Promise<CharacterEqTemplateResult>((resolve,error)=>{
            this.URI = "https://api.guildwars2.com/v2/characters/" + characterName + "/equipmenttabs/" + templateId;
            this.call().then((res)=>{
                const result : CharacterEqTemplateResult = {CharacterName : characterName, EqTemplateId : templateId, Equipment : JSON.parse(res)};
                resolve(result);
            }).catch(err=>error(err));
        });
    }

    GetItemData(itemId: number) : Promise<ItemDataResult>{
        return new Promise<ItemDataResult>((resolve,error)=>{
            this.URI = "https://api.guildwars2.com/v2/items/" + itemId;
            this.call().then((res)=>{
                let jsonRes = JSON.parse(res);
                const result : ItemDataResult = {ID : itemId, Name : jsonRes.name, IconUrl : jsonRes.icon, Type: jsonRes.type};
                resolve(result);
            }).catch(err=>error(err));
        });
    }

}